<template>
  <div class="wallet">
    <el-form :inline="true">
      <el-row>
        <div class="wallet__card">
          <div class="wallet__card-head">余额</div>
          <div class="wallet__card-body">￥{{ userBalance ? userBalance : "0.00" }}</div>
        </div>
      </el-row>
      <el-row>
        <el-button
          class="wallet__btn--add"
          type="primary"
          size="mini"
          @click="submit()"
          >提现</el-button
        >          
      </el-row>
      <el-form-item>
        <el-select size="mini" v-model="search.action" @change="searchWallet">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <el-tabs v-model="search.status" type="card" @tab-click="searchWallet">
      <el-tab-pane label="全部" name="-1"></el-tab-pane>
      <el-tab-pane label="等待" name="0"></el-tab-pane>
      <el-tab-pane label="完成" name="1"></el-tab-pane>
      <el-tab-pane label="失败" name="2"></el-tab-pane>
    </el-tabs>
    <el-table :data="walletList" style="width: 100%" border stripe height="450">
      <el-table-column prop="wallet_id" label="钱包ID" width="80" align="center"></el-table-column>
      <el-table-column prop="title" label="流水类型" width="250"></el-table-column>
      <el-table-column label="备注">
        <template slot-scope="{ row }">
          {{ row | changeJson }}
        </template>
      </el-table-column>
      <el-table-column prop="amount" label="流水(元)" width="200" align="right">
        <div :class="{ 'getMoney': row.state == 1 }" class="wallet__balance" slot-scope="{ row }">
          {{ row.state == 1 ? '+' : '-' }} {{ row.amount | t2 }}
        </div>
      </el-table-column>
      <el-table-column prop="status" label="状态" width="80" align="center">
        <template slot-scope="{ row }">
          <el-tag :type="row.status | changeTag">
            {{ row.status | changeStatus }}
          </el-tag>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="search.page"
      :page-size="search.limit"
      layout="total, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>
<script>
import { walletList } from "@/api/wallet.js";

export default {
  name: "Wallet",
  data() {
    return {
      search: {
        page: 1,
        limit: 15,
        status: '-1',
        action: '0'
      },
      walletList: [],
      total: 0,
      options: [
        {
          value: "0",
          label: "全部"
        },
        {
          value: "1",
          label: "收入"
        },
        {
          value: "2",
          label: "支出"
        }
      ]
    };
  },
  watch: {
    "$route"(to) {
      if(to.name == 'wallet') {
        this.getWalletList();
      }
    }
  },
  computed: {
    userBalance: {
      get() {
        return this.$store.state.user.balance;
      },
      set(val) {
        this.$store.commit("user/updateBalance", val);
      }
    }
  },
  filters: {
    changeJson(val) {
      if(val.type === 1) {
        const address = JSON.parse(val.data).address;
        return (address ? address.name : "") + '/' + (address ? address.account_number : "");
      }else {
        return JSON.parse(val.data).goods_name;
      }
    },
    t2(val) {
      return parseFloat(val).toFixed(2);
    },
    changeStatus(val) {
      switch(val) {
        case 0:
          return "等待";
        case 1:
          return "完成";
        case 2:
          return "失败";
      }
    },
    changeTag(val) {
      switch(val) {
        case 0:
          return "info";
        case 1:
          return "success";
        case 2:
          return "danger";
      }
    }
  },
  methods: {
    handleCurrentChange(page) {
      this.search.page = page;
      this.getWalletList();
    },
    searchWallet() {
      this.search.page = 1;
      this.getWalletList();
    },
    getWalletList() {
      walletList(this.search).then(({ data: { data, total } }) => {
        this.walletList = data;
        this.total = total;
      })
    },
    submit() {
      this.$router.push({ name: "withdraw" });
    }
  },
  mounted() {
    this.getWalletList();
  }
};
</script>

<style lang="scss">
$prefix: "wallet";

.#{$prefix} {
  &__card {
    background: linear-gradient(-125deg, #57bdbf, #2f9de2);
    min-height: 140px;
    color: #ffffff;
    padding: 12px 17px 12px 22px;
    margin-bottom: 20px;
    box-sizing: border-box;
    &-head {
      position: relative;
      display: block;
      padding-top: 18px;
      color: #fff;
      font-size: 1.4rem;
      text-transform: uppercase;
      margin-bottom: 8px;
      box-sizing: border-box;
    }
    &-body {
      position: relative;
      font-weight: 300;
      display: block;
      color: #fff;
      font-size: 46px;
      line-height: 46px;
      margin-bottom: 8px;
    }
  }
  &__balance {
    color: red;
  }
  .getMoney {
    color: #67C23A;
  }
  &__btn--add {
    margin-bottom: 20px;
  }
}
</style>
